<template>
  <div>
    <div style="margin-bottom:60px; margin-top:60px">
      <h1>Recuperacion de contraseña</h1>
    </div>
    <v-form
      ref="form"
      class="mt-5 mb-4"
      v-model="valid"
      lazy-validation
      >
      <v-text-field
        v-model="email"
        label="Email"
        type="email"
        dense
        outlined
        :rules="emailRules"
        >
      </v-text-field>
      <div>
        <v-btn @click="recover" color="accent" width="70%" :loading="processing">recuperar</v-btn><br>
      </div>
      <v-divider class="my-4"></v-divider>
        <v-btn @click="$emit('change-mode', 'login')" color="secondary" width="70%">volver</v-btn><br>
    </v-form>
  </div>
</template>
<script>
import { getAuth, sendPasswordResetEmail } from 'firebase/auth'
export default {
  data () {
    return {
      email: '',
      emailRules: [
        v => !!v || 'El email es requerido',
        v => /.+@.+/.test(v) || 'El email debe ser valido'
      ],
      processing: false,
      valid: false
    }
  },
  methods: {
    async recover () {
      this.processing = true
      this.valid = await this.$refs.form.validate()
      if (this.valid) {
        try {
          const auth = getAuth()
          await sendPasswordResetEmail(auth, this.email)
          this.$toasted.success('Email con instrucciones enviado..', { icon: 'done', duration: 3000 })
        } catch (error) {
          console.log(error)
          const errorCode = error.code
          console.log(errorCode)
          if (errorCode === 'auth/user-not-found') this.$toasted.error('Email no encontrado.', { icon: 'error', duration: 3000 })
          else this.$toasted.error('Error en recuperación de email', { icon: 'error', duration: 3000 })
        }
      }
      this.processing = false
    }
  }
}
</script>
